<script>
import {defineComponent, h, toRef} from 'vue'
import { RouterLink } from 'vue-router'
import { mixinColor } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

export default defineComponent({
  name: 'Breadcrumb',
  mixins: [
    ...addMixinPrefix([
      mixinColor
    ], 'breadcrumb')
  ],
  props: {
    items: {
      type: Array,
      default: () => ([])
    }
  },
  setup(props, { slots }) {
    const genItems = () => {
      let items = []

      items.push(h('li', {
        class: 'breadcrumb-item'
      }, h(RouterLink, {
        to: {
          name: 'dashboard'
        }
      }, {
        default: () => h('span', {
          class: ['material-icons', 'icon-md']
        }, {
          default: () => 'home'
        })
      })))

      items.push(h('li', {
        class: [
          'breadcrumb-item',
          'breadcrumb-item__divider'
        ]
      }, slots.default && slots.default() || '/'))

      props.items.forEach((item, index) => {
        const isLast = index === props.items.length - 1
        items.push(h('li', {
          class: 'breadcrumb-item'
        }, [!isLast && h(RouterLink, {
          to: item.to
        }, {
          default: () => item.text
        }) || item.text]))
        if(!isLast) items.push(h('li', {
          class: [
            'breadcrumb-item',
            'breadcrumb-item__divider'
          ]
        }, {
          default: () => slots.default && slots.default() || '/'
        }))
      })
      return items
    }

    return {
      genItems
    }
  },
  render() {
    this.genItems()
    const children = [
      this.genItems()
    ]
    const listClass = [
      'breadcrumb',
      this.classColor
    ]

    return h('ul', {
      class: listClass
    }, children)
  }
})
</script>